@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css?family=Raleway:200,300,400&display=swap);
@import url(https://fonts.googleapis.com/css?family=Fjalla+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato:300,400&display=swap);
@import url(https://fonts.googleapis.com/css?family=Comfortaa:700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Anton&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "Open Sans", "Poppins", "Raleway", "Fjalla One", "Montserrat",
  "Lato", "Comfortaa", "Anton",
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
  monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #2B2F77;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #2B2F77;
}

.Menu-icon {
  position: absolute;
  left: 1%;
  top: 2%;
  right: 0;
}

.menu {
    font-family: sans-serif;
}

.menu-item {
    color: #c0c3c4;
    border-bottom: 0px solid #c0c3c4;
}

.menu-item a {
    display: block;
    padding: .9rem 15px;
    text-decoration: none;
    font-family: Poppins;
    color: #c0c3c4;
    font-size: x-large;
    font-min-size: x-large;
}

.menu-item a:hover {
    color: white;
    text-decoration: none;
    background-color: #a5a7bd;
}

.menu-item a:focus {
    text-decoration: none;
}

.active-menu-item {
    background-color: #757793;
}

.hint {
    padding: .0rem 15px;
    font-family: Open Sans;
    color: #c0c3c4
}

